.scissors {
  background-color: green;
  display: inline;
}

.clipperz-app {
  background-color: teal;
  text-align: center;
}

img {
  width: 90px;
  
  animation: rotation 1s;
  transform: rotate(40deg);
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(40deg);
  }
}